@import '../core/Variables.scss';
@import '../core/Mixins.scss';

.section {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.link {
    box-shadow: -5px -5px 30px 5px #03B0C8, 5px 5px 30px 5px #8FDD7F;
    font-size: 40px;
    color: whitesmoke;
    padding-left: 20px;
    padding-right: 20px;
    @include link;
    
    
}

.detail{
    display: flex;
    margin-bottom: 0;
    text-decoration: none;
    justify-content: center;
    
    

    &__card {
        list-style: none;
        background-color: $color-card;
        text-align: center;
        
        
    }

   
    &__name{
        color: $color-detailRest;
        font-size: 30px;
    }
    &__rest {
        text-decoration: none;
        color: $color-letter;
        
            &--extra{
            text-decoration: underline;
            margin-right: 5px;
            color: $color-detailRest;
            }
    }
}