@import '../core/Mixins.scss';
@import '../core/Variables.scss';


.charList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 45px;
    text-decoration: none;
    padding-left: 0;
    margin-bottom: 0;
    padding-bottom: 25px;
        &__card {
            list-style: none;
            background-color: $color-card;
            text-align: center
        }
        &__link {
            @include link;
            &--img {
                object-fit: cover;
            }
            &--detail{
                text-decoration: none;
                color: $color-letter;
            }
        }
}